import React from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  useLocation
} from "react-router-dom";
import { Navbar, Nav, Container } from "react-bootstrap";

export default function MyNavbar(props) {
  const location = useLocation();

  return (
    <Navbar bg="dark" variant="dark">
      <Container style={{ marginLeft: 2 }}>
        {(location.pathname === "/" ||
          location.pathname === "/IncidentManager") && (
          <Navbar.Brand>Incident Manager</Navbar.Brand>
        )}
        {location.pathname === "/NCRProjectTracker" && (
          <Navbar.Brand>NCR Project Tracker</Navbar.Brand>
        )}

        <Nav className="me-auto">
          {(location.pathname === "/" ||
            location.pathname === "/IncidentManager") && (
            <Nav.Link as={Link} to="/NCRProjectTracker">
              NCR Project Tracker
            </Nav.Link>
          )}
          {location.pathname === "/NCRProjectTracker" && (
            <Nav.Link as={Link} to="/IncidentManager">
              Incident Manager
            </Nav.Link>
          )}
        </Nav>
      </Container>
    </Navbar>
  );
}
